<template>
    <div class="ymf-calender-config">
        <div class="field-title"></div>
        <br>
        <div style="width: 90%;margin: 0 auto;">
            <el-form label-width="120px" label-suffix="">
                <el-form-item label="">
                    <el-input type="text" v-model="source.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="source.placeholder" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="source.calenderType">
                        <el-radio label="single"></el-radio>
                        <el-radio label="multiple"></el-radio>
                        <el-radio label="range"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker v-model="source.minDate" type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker v-model="source.maxDate" type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker v-model="source.defaultDate" type="date" placeholder="" @change="changeDate"></el-date-picker>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
	export default {
		name: "ymf-calender-config",
		props:{
			source: {
				type: Object,
				required: true
			}
		},
        methods: {
			changeDate(date) {
				this.source.value = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
            }
        }
	}
</script>

<style lang="less" scoped>
    .ymf-calender-config {
        padding: 5px;
    }
    .field-title{
        text-align: left;
        font-weight: 700;
        font-size: 18px;
    }
</style>