<template>
    <div class="ymf-field-config">
        <div class="field-title"></div>
        <br>
        <div style="width: 80%;margin: 0 auto;">
            <el-form label-width="120px" label-suffix="">
                <el-form-item label="">
                    <el-input v-model="source.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="source.fieldType" @change="checkType">
                        <el-radio label="text"></el-radio>
                        <el-radio label="number"></el-radio>
                        <el-radio label="textarea"></el-radio>
                        <el-radio label="password"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="source.placeholder" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="source.clearable" active-text="" inactive-text=""></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="source.disabled" active-text="" inactive-text=""></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="source.readonly" active-text="" inactive-text=""></el-switch>
                </el-form-item>
                <el-form-item label="" v-show="isArea">
                    <el-switch v-model="source.showWordLimit" active-text="" inactive-text=""></el-switch>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
	export default {
		name: "ymf-field-config",
		props:{
			source: {
				type: Object,
				required: true
			}
		},
        data() {
			return {
				isArea: false
            }
        },
        methods: {
			checkType(type) {
				if (type === 'textarea') {
					this.isArea = true
                } else {
					this.isArea = false
                    this.source.showWordLimit = false
                }
            }
        }
	}
</script>

<style scoped>
    .ymf-field-config {
        padding: 5px;
    }
    .field-title{
        text-align: left;
        font-weight: 700;
        font-size: 18px;
    }
</style>